  .members-section {
    width: 100%;
    text-align: center;
    background-color: #f4f4f4;
  }

  .main-image {
    width: 100%;
    height: auto;
    max-height: 400px;
    object-fit: cover;
    margin-bottom: 20px;
  }

  .professors-list {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }

  .professor {
    display: flex;
    align-items: center;
    width: 600px;
    background-color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: left;
    padding: 15px;
    border-radius: 8px;
    margin-bottom: 20px;
  }

  .profile-image {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 8px;
    margin-right: 20px;
  }

  .research-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  /* 슬라이드쇼 스타일 */
  .members-list {
    width: 100%;
    max-width: 850px;
    margin: 0 auto;
    margin-bottom: 20px;
    overflow: hidden; /* 슬라이드 영역을 벗어나는 부분 숨기기 */
  }

  .slick-slide {
    display: flex !important;
    justify-content: center;
    align-items: center;
  }
  

  .member {
    width: 90%; /* 슬라이드 너비를 90%로 설정해 좌우 여백 확보 */
    max-width: 350px; /* 최대 너비를 제한 */
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    margin: auto;
  }

  .member .profile-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    margin-bottom: 10px;
  }

  .member h3 {
    font-size: 1.1rem;
    font-weight: bold;
    color: #333;
    margin: 10px 0;
  }

  .member p {
    font-size: 0.9rem;
    color: #666;
  }

  /* 슬라이드 버튼 커스터마이징 */
  .slick-prev,
  .slick-next {
    font-size: 0;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    color: black;
    z-index: 1;
  }

  .slick-prev:before,
  .slick-next:before {
    font-size: 24px;
    color: black;
  }


  .slick-next{
    right: 0px;
  }

  .slick-prev{
    left: 0px;
  }

  /* 반응형 스타일 */
  @media (max-width: 768px) {
    .professor, .member {
      width: 90%;
    }

    .profile-image {
      width: 120px;
      height: 120px;
      margin-right: 15px;
    }

    .main-image {
      max-height: 250px;
    }
  }

  @media (max-width: 480px) {
    .professor, .member {
      width: 100%;
    }

    .profile-image {
      width: 100px;
      height: 100px;
      margin-right: 10px;
    }

    .main-image {
      max-height: 200px;
    }

    .slick-prev, .slick-next {
      width: 20px;
      height: 20px;
    }

    .slick-prev:before, .slick-next:before {
      font-size: 16px;
    }
  }