.contact-section {
    text-align: center;
  }
  
  .contact-title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .contact-info {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 80px;
    gap: 30px;
  }
  
  .contact-item {
    display: flex;
    align-items: center;
    width: 300px;
    text-align: center;
    gap: 15px;
    align-items: center;
  }
  
  .contact-icon {
    font-size: 1.5rem;
    color: #2b2b2b;
    margin-top: 5px;
  }
  
  .contact-name {
    font-weight: bold;
    font-size: 1.1rem;
  }
  